export default {
  name: "Features",
  props: {
    info: { type: Object, required: true },
    index: { type: Number, required: true },
  },
  data() {
    return {     
      title: this.info.title || '',
      value: this.info.value || '',
    }
  },
  methods: {
    updateValue() {
      this.$emit('update', { index: this.index, title: this.title, value: this.value })
    }
  }
}
